import {Injectable} from '@angular/core';
import {AppTheme} from 'src/app/core/store/app/app.state';

// enum ThemeType {
// 	dark = 'dark',
// 	default = 'default'
// }

@Injectable({
	providedIn: 'root'
})
export class ThemeService {
	currentTheme ='default';

	constructor() {}

	public loadTheme(firstLoad = true, effectiveTheme: AppTheme): Promise<Event> {
		const theme = effectiveTheme; //this.currentTheme;
		//console.log(theme);
		if (firstLoad) {
			document.documentElement.classList.add(theme);
		}
		return new Promise<Event>((resolve, reject) => {
			this.loadCss(`${theme}.css`, theme).then(
				(e) => {
					if (!firstLoad) {
						document.documentElement.classList.add(theme);
					}
					this.removeUnusedTheme(this.reverseTheme(theme));
					resolve(e);
				},
				(e) => reject(e)
			);
		});
	}
	public loadThemeOLD(firstLoad = true): Promise<Event> {
		const theme = this.currentTheme;
		console.log(theme);
		if (firstLoad) {
			document.documentElement.classList.add(theme);
			console.log(theme);
		}
		return new Promise<Event>((resolve, reject) => {
			this.loadCss(`${theme}.css`, theme).then(
				(e) => {
					if (!firstLoad) {
						document.documentElement.classList.add(theme);
						console.log(theme);
					}
					this.removeUnusedTheme(this.reverseTheme(theme));
					resolve(e);
				},
				(e) => reject(e)
			);
		});
	}

	public toggleTheme(): Promise<Event> {
		this.currentTheme = this.reverseTheme(this.currentTheme);
		return this.loadThemeOLD(false);
	}
	private reverseTheme(theme: string): AppTheme {
		return theme === 'dark' ?  'default' :  'dark';
	}

	private removeUnusedTheme(theme: AppTheme): void {
		document.documentElement.classList.remove(theme);
		const removedThemeStyle = document.getElementById(theme);
		if (removedThemeStyle) {
			document.head.removeChild(removedThemeStyle);
		}
	}
	//tạo element và gắn vào head
	private loadCss(href: string, id: string): Promise<Event> {
		return new Promise((resolve, reject) => {
			const style = document.createElement('link');
			style.rel = 'stylesheet';
			style.href = href;
			style.id = id;
			style.onload = resolve;
			style.onerror = reject;
			document.head.append(style);
			//console.log(style);
		});
	}
}
